.listItem {
  min-height: 80px;
  border-bottom: 1px solid #f5f5f5;
  padding: 10px 0;
  margin-bottom: 15px;
}
.listItemTitle {
  font-size: 18px;
  color: #333;
  overflow: hidden;
  height: 50px;
  line-height: 50px;
}
.listItemDescription {
  font-size: 12px;
  height: 30px;
  line-height: 30px;
  color: #999;
  transform: scale(0.8);
  transform-origin: 0%;
}
.listItemDetail {
  font-size: 12px;
  color: #AAA;
}
